function getDataFromServer(list, total) {
	return function (dispatch) {
		axios.get("/api/data/sample", {
			params: { name: list }
		}).then(function (response) {
			var data = response.data;
			// console.log('fomrServer', data)
			dispatch({
				type: "ADD_DATA_SAMPLES",
				payload: {
					sampleList: data.sampleList,
					sampleData: data.sampleData,
					total: total
				}
			});
		}).catch(function (e) {
			dispatch({
				type: "ADD_DATA_SAMPLES_REJECTED",
				payload: e
			});
		});
	};
}

export function AddData(action, sampleNames) {
	return function (dispatch, getState) {
		if (action === "ADD") {
			(function () {
				var dataBySamples = getState().data.dataBySamples;
				var dataToQuery = [];
				var dataHave = Object.keys(dataBySamples);

				sampleNames.forEach(function (name) {
					if (dataHave.findIndex(function (d) {
						return d === name;
					}) === -1) dataToQuery.push(name);
				});

				if (dataToQuery.length) {
					var _loop = function _loop(i) {
						var sliced = dataToQuery.slice(i, i + MAX_NUM_SAMPLE_QUERY);
						setTimeout(function () {
							dispatch(getDataFromServer(sliced, dataToQuery.length));
						}, SAMPLE_TIMEOUT);
					};

					for (var i = 0; i < dataToQuery.length; i += MAX_NUM_SAMPLE_QUERY) {
						_loop(i);
					}
				}
			})();
		}
	};
}

